/* General App Styling */
.App {
  display: flex;
  height: 100vh;
  background-color: #1a1a1a;
  color: #f5f5f5;
}

/* Sidebar Styling */
.sidebar {
  width: 250px;
  background-color: #252525;
  padding: 20px;
  display: flex;
  flex-direction: column;
  border-right: 1px solid #333;
}

.sidebar h1 {
  font-size: 1.5em;
  margin-bottom: 20px;
  text-align: center;
}

.sidebar nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar nav ul li a {
  color: #f5f5f5;
  text-decoration: none;
  display: block;
  padding: 10px 15px;
  border-radius: 5px;
  margin-bottom: 10px;
  transition: background-color 0.3s;
}

.sidebar nav ul li a:hover {
  background-color: #333;
}

.sidebar .logout-button {
  margin-top: auto;
  background-color: #d9534f;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.sidebar .logout-button:hover {
  background-color: #c9302c;
}

/* Main Content Styling */
.main-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

/* Chat Window Styling */
.chat-window {
  flex-grow: 1;
  overflow-y: auto;
  padding: 30px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.message {
  padding: 15px 20px;
  border-radius: 20px;
  max-width: 80%;
  word-wrap: break-word;
  line-height: 1.6;
}

.message.user {
  background-color: #3b82f6;
  color: white;
  align-self: flex-end;
  border-bottom-right-radius: 5px;
}

.message.bot {
  background-color: #374151;
  color: #f5f5f5;
  align-self: flex-start;
  border-bottom-left-radius: 5px;
}

/* Input Form Styling */
.input-form {
  display: flex;
  padding: 20px;
  border-top: 1px solid #333;
  background-color: #252525;
}

.input-form input {
  flex-grow: 1;
  padding: 15px;
  border: 1px solid #444;
  border-radius: 8px;
  margin-right: 15px;
  background-color: #374151;
  color: #f5f5f5;
  font-size: 1em;
}

.input-form button {
  padding: 15px 25px;
  background-color: #3b82f6;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.input-form button:hover {
  background-color: #2563eb;
}

.input-form button:disabled {
  background-color: #555;
  cursor: not-allowed;
}

/* Loader */
.loader {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  animation: spin 1s linear infinite;
  margin-left: 10px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Upload Forms Styling */
.upload-form-container {
  background-color: #252525;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  max-width: 600px;
  margin: 50px auto;
  text-align: center;
}

.upload-form-container h2 {
  color: #f5f5f5;
  margin-bottom: 30px;
}

.upload-form-container form {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.upload-form-container .form-group {
  text-align: left;
}

.upload-form-container label {
  margin-bottom: 10px;
  font-weight: bold;
  color: #ccc;
  display: block;
}

.upload-form-container input[type="text"],
.upload-form-container input[type="url"],
.upload-form-container input[type="file"] {
  width: 100%;
  padding: 12px;
  border: 1px solid #444;
  border-radius: 5px;
  background-color: #374151;
  color: #f5f5f5;
  box-sizing: border-box;
}

.upload-form-container button {
  padding: 15px 30px;
  background-color: #3b82f6;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.1em;
  transition: background-color 0.3s ease;
  align-self: center;
}

.upload-form-container button:disabled {
  background-color: #555;
  cursor: not-allowed;
}

.upload-form-container button:hover:not(:disabled) {
  background-color: #2563eb;
}